@import 'mixins';

body {
  font-family: $montserrat-font-family;
  user-select: none;
  color: #000000;
  background-color: $grey_color;
}

* {
  scrollbar-width: thin;
}

.section {
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
}

ul,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul {
  list-style-type: none;
  padding-inline-start: unset;
}

img {
  width: 100%;
  height: auto;
}

a,
a:hover {
  text-decoration: none;
}

a,
button {
  cursor: none;
}

textarea {
  resize: none;
}

a:focus,
button:focus,
label:focus,
input:focus,
textarea:focus {
  outline: none;
}

::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  border-radius: 4px;
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background: #c1c1c1;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.25);
}

@media (min-width: 576px) {
  .section {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .section {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .section {
    max-width: 960px;
  }

  a,
  button {
    cursor: pointer;
  }
}

@media (min-width: 1200px) {
  .section {
    max-width: 1140px;
  }
}
